import React, { Component } from "react";
import { Button, Row, Col, Form, FormGroup, Input, Label } from "reactstrap";
import img from "../../assets/images/logo-white.svg";
import InputBanky from "../../components/inputBanky/InputBanky";
import Validator from "./validator";

export class queueForm extends Component {
  state = {
    cnpj: "",
    email: "",
    celular: "",
    url: "",
    nome: ""
  };
  constructor(props) {
    super(props);
    this.validators = Validator;
    this.nextStep = this.props.nextStep;
  }

  handleChange(e) {
    let change = {};
    change[e.target.name] = e.target.value;
    this.setState(change);
  }
  validForm() {
    let status = true;
    const states = this.state;
    Object.keys(this.validators).forEach(field => {
      this.formValidators(field, this.state[field]);
      console.log("FormFieldValidation:", field, this.validators[field].valid);
      if (!this.validators[field].valid) {
        status = false;
      }
    });
    return status;
  }
  formValidators(fieldName, value) {
    this.validators[fieldName].errors = [];
    this.validators[fieldName].state = value;
    this.validators[fieldName].valid = true;
    this.validators[fieldName].rules.forEach(rule => {
      if (rule.test instanceof RegExp) {
        if (!rule.test.test(value)) {
          this.validators[fieldName].errors.push(rule.message);
          this.validators[fieldName].valid = false;
        }
      } else if (typeof rule.test === "function") {
        if (!rule.test(value)) {
          this.validators[fieldName].errors.push(rule.message);
          this.validators[fieldName].valid = false;
        }
      }
    });
    this.setState(this.state);
  }
  showErrors(fieldName) {
    if (fieldName=="")
      return <div className="error"></div>;
    const validator = this.validators[fieldName];
    const result = "";

    if (validator && !validator.valid) {
      const errors = validator.errors.map((info, index) => {
        return (
          <span className="error" key={index}>
            <i className="mdi mdi-alert-octagram" /> {info}
            <br />
          </span>
        );
      });
      return <div className="error">{errors}</div>;
    }
    return result;
  }
  handleClick(e) {
    e.preventDefault();
    let res = this.validForm();
    console.log("ValidForm:", res);
    if (res) this.nextStep();
  }
  render() {
    return (
      <Row
        noGutters
        className="justify-content-center align-items-stretch QueueForm"
      >
        <Row
          noGutters
          className="col-12 col-md-8 col-lg-4 col-xl-4 wrapper justify-content-center align-items-stretch"
        >
          <Row noGutters className="justify-content-center align-items-center">
            <Col xs="12" className="text-center">
              <img className="logo" src={img} alt="Logo Banky" />
            </Col>
            <Col xs="12">
              <div className="text-center introduction">
                Chega de misturar as contas pessoais com contas corporativas!
                Preencha os dados abaixo e entre em nossa lista de espera.
              </div>
            </Col>
          </Row>
          <Form className="form-material w-100">
            <Row noGutters>
              <Col xs="12">
                <InputBanky
                  label="CNPJ"
                  type="text"
                  onChange={this.handleChange.bind(this)}
                  value={this.state.cnpj}
                  name="cnpj"
                  formNoValidate
                  required
                />
                {this.showErrors("cnpj")}
              </Col>
            </Row>
            <Row noGutters>
              <Col xs="12">
                <InputBanky
                  label="E-mail"
                  onChange={this.handleChange.bind(this)}
                  type="email"
                  value={this.state.email}
                  name="email"
                  formNoValidate
                  required
                />
                {this.showErrors("email")}
              </Col>
            </Row>
            <Row>
              <Col xs="12" md="6">
                <InputBanky
                  label="Celular"
                  type="tel"
                  onChange={this.handleChange.bind(this)}
                  value={this.state.celular}
                  name="celular"
                  formNoValidate
                  required
                />
                {this.showErrors("")}
              </Col>
              <Col xs="12" md="6">
                <InputBanky
                  label="Site (Opcional)"
                  type="url"
                  onChange={this.handleChange.bind(this)}
                  value={this.state.url}
                  formNoValidate
                  name="url"
                />
                {this.showErrors("")}
              </Col>
            </Row>
            <Row>
              <Col xs="12">
                <InputBanky
                  label="Nome para contato"
                  type="text"
                  onChange={this.handleChange.bind(this)}
                  value={this.state.nome}
                  formNoValidate
                  name="nome"
                />
                {this.showErrors("")}
              </Col>
              
            </Row>
            <Button
              size="lg"
              block
              type="submit"
              onClick={this.handleClick.bind(this)}
              className="btn btn-light text-primary"
            >
              Entrar na lista de espera
            </Button>
          </Form>
        </Row>
      </Row>
    );
  }
}

export default queueForm;



