import React, { Component } from "react";
import { Button, Row, Col, Form, FormGroup, Input, Label } from "reactstrap";
import img from "../../assets/images/logo-white.svg";
import InputBanky from "../../components/inputBanky/InputBanky";
import Validator from "./validator";

export class queueDone extends Component {
  constructor(props) {
    super(props);
    this.validators = Validator;
    this.nextStep = this.props.nextStep;
  }
  handleClick = () => {
    console.log("considere-se clicado");
  };
  render() {
    return (
      <Row
        noGutters
        className="justify-content-center align-items-stretch QueueDone"
      >
        <Row
          noGutters
          className="col-12 col-md-8 col-lg-4 col-xl-4 wrapper justify-content-center align-items-stretch"
        >
          <Row noGutters className="justify-content-center align-items-center">
            <Col xs="12" className="text-center">
              <img className="logo" src={img} alt="Logo Banky" />
            </Col>
            <Col xs="12" className="text-center">
              <img
                src="http://iprism.co.in/wp-content/uploads/2017/12/9.png"
                height="300"
                alt=""
                srcset=""
              />
            </Col>
            <Col>
            <h1 className="text-center title">Parabéns!</h1>
            </Col>
            <Col xs="12">              
              <p className="text-center description">
                Você está em nossa lista de espera Banky, e em breve entraremos
                em contato.
              </p>
              <Button
                size="lg"
                block
                type="button"
                onClick={this.handleClick.bind(this)}
                className="btn btn-outline-light"
              >
                Voltar para a home
              </Button>
            </Col>
          </Row>
        </Row>
      </Row>
    );
  }
}

export default queueDone;



